import { createStore } from 'vuex';

export default createStore({
    state:{
        msg: 'Vuex desde Cero / Vuex from the scratch',
        cart: [],
        copyCart: [],
        item: {},
        numberCart: 0,
        menuMobile: false,
        isWildScreen: false,
    },
    mutations:{
        handleRemoveItemCart( state ){
            state.cart = state.copyCart
            state.numberCart = state.cart.length;
        },
        handleAddCart( state ){
            if(state.cart === null) {
                state.cart = [];
            }
            state.cart = [state.item, ...state.cart]
            localStorage.setItem('cart', JSON.stringify(state.cart));
            state.numberCart = state.cart.length;
        },
        handleMenuMobile( state ){
            state.menuMobile = !state.menuMobile
        },
        handleResize( state ){
            if(state.isWildScreen){
                state.menuMobile = false;
            }
        }
    },
    actions:{
        removeItemCartAction( context ){
            context.commit('handleRemoveItemCart')
        },
        addCartAction( context ){
            context.commit('handleAddCart')
        },
        activeMenuMobileAction( context ){
            context.commit('handleMenuMobile')
        },
        handleResizeAction( context ){
            context.commit('handleResize')
        }
    },
    getters:{
        getCart(state){
            state.cart = JSON.parse(localStorage.getItem('cart'));
            if(state.cart !== undefined && state.cart !== null && state.cart.length > 0){
                return state.cart;
            }
        },
    }
})