<template>
  <header class="header">
    <div class="header__container container">
      <span class="header__logo"><router-link class="nav-bar__link" to="/"><i class='bx bxs-store-alt'></i></router-link></span>
      <span class="header__ham" v-if="!$store.state.menuMobile" v-on:click="handleToggleMenu"><i class='bx bx-menu-alt-right'></i></span>
      <span class="header__ham header__ham--close" v-else v-on:click="handleToggleMenu"><i class='bx bx-x'></i></span>
      <div class="header__menu" v-bind:class="$store.state.menuMobile ? 'header__menu--active' : '' ">
        <NavigationBarComponent></NavigationBarComponent>
        <OptionsComponent></OptionsComponent>
      </div>
    </div>
  </header>
</template>

<script>
import NavigationBarComponent from './NavigationBarComponent.vue';
import OptionsComponent from './OptionsComponent.vue';
export default {
  name: 'HeaderComponent',
  components: {
    NavigationBarComponent,
    OptionsComponent
  },
  data() {
    return {
      // Datos del componente
      pieces: null,
      itemsNum: null,
      isWideScreen: null,
    };
  },
  methods: {
    // Métodos del componente
    handleToggleMenu(){
      this.$store.dispatch('activeMenuMobileAction');
    },
    handleResize() {
      this.$store.state.isWildScreen = window.innerWidth > 768;
      this.$store.dispatch('handleResizeAction');
    }
  },
  mounted() {
    window.addEventListener("resize", this.handleResize);
  },
  unmounted() {
    window.removeEventListener("resize", this.handleResize);
  },
};
</script>

<style>
/* Estilos del componente */
  .header{
    background-color: var(--black);
    padding-block: 2rem;
  }
  .header__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__logo{
    color: var(--white);
    font-size: 3rem;
  }
  .header__ham{
    display: block;
    color: var(--white);
    font-size: 3rem;
    grid-area: h-ham;
    justify-self: flex-end;
  }
  .header__menu{
    position: absolute;
    left: -100%;
    transition: left 200ms ease;
    filter: blur(10px);
  }
  .header__menu--active{
    top: 0;
    left: 0;
    opacity: 1;
    background-color: var(--black-secondary);
    height: 100dvh;
    width: 65%;
    z-index: 10;
    filter: blur(0);
  }
  .header__ham--close{
    z-index: 10;
  }
  @media (min-width: 768px){ 
    .header__ham{
      display: none;
    }
    .header__menu{
      all: inherit;
      display: flex;
      flex-grow: 1;
    }
    .header__menu--active{
      display: none;
    }
  }
</style>